import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from "@angular/material/dialog";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  FormArray
} from "@angular/forms";
import {Router } from '@angular/router';
import * as moment from 'moment';

@Component({
  selector: 'app-add-edit-Compaign',
  templateUrl: './add-edit-Compaign.component.html',
  styleUrls: ['./add-edit-Compaign.component.scss']
})
export class AddEditCompaignComponent implements OnInit {
productForm: FormGroup;

  productList: any = [
    "Product 1",
    "Product 2",
    "Product 3",
    "Product 4",
    "Product 5"
  ]

  prizeList: any = [
    "Prize 1",
    "Prize 2",
    "Prize 3",
    "Prize 4",
    "Prize 5"
  ]
  onStatus: boolean = false;
  onDonateStatus: boolean = false;
  productImage: any;
  prizeImage: any;
  submitted: boolean = false;
  history = window.history;
  startMinDate = moment(new Date()).subtract(1, 'd').format('YYYY-MM-DD')
  constructor( private formBuilder: FormBuilder, private router:Router) { }

  ngOnInit() {
    this.productForm = this.formBuilder.group({
      name: new FormControl("", [Validators.required]),
      arabicName: new FormControl("", [Validators.required]),
      productName: new FormControl("", [Validators.required]),
      quantity: new FormControl("", [Validators.required]),
      image: new FormControl("", [Validators.required]),
      earlyBird: new FormControl(),
      donate: new FormControl(),
      itemNumber: new FormControl("", [Validators.required]),
      ticketPerProduct: new FormControl("", [Validators.required]),
      ticketAccount: new FormControl("", [Validators.required]),
      perProductDonate:new FormControl("", [Validators.required]),
      maxUserProduct: new FormControl(""),
      prizeName:new FormControl("", [Validators.required]),   
      startDate: new FormControl("", Validators.compose([Validators.required]))
    });
  }

  async profilePic(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        this.productImage = event.target.result;
        this.productForm.controls['image'].setValue(this.productImage);
      };
    }
  } 

  onSubmit(){
    this.submitted = true;
    if(this.submitted && this.productForm.valid){ 
      var data = this.productForm.value; 
      if(data.earlyBird == false){
        data['itemNumber'] =0
      }
      data['status'] =false;
     this.router.navigate(['/Compaign/Compaign'])
    }
  }

  onChange(event){
    this.onStatus = event;
  }

  onDonateChange(event){
    this.onDonateStatus = event;
  }

  afterPickerOpen() {
    document.getElementsByTagName('html')[0].style.top = '';
  }
}
 
