import { Injectable } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { AcceptModalComponent } from "../../pages/main/order/accept-modal/accept-modal.component";
import { AddEditInventoryComponent} from '../../pages/inventory/add-edit-inventory/add-edit-inventory.component';
import { AddEditCouponComponent} from '../../pages/coupon/add-edit-coupon/add-edit-coupon.component';
import { AddEditCharityComponent } from '../../pages/charity/add-edit-charity/add-edit-charity.component';
import { AddEditCompaignComponent } from '../../pages/Compaign/add-edit-Compaign/add-edit-Compaign.component';
import { AddLoyaltyComponent } from '../..//pages/loyality/add-loyalty/add-loyalty.component';

@Injectable({
  providedIn: "root"
})
export class PopupService {
  constructor(public dialog: MatDialog) { }

  orderAcceptModal() {
    const dialogRef = this.dialog.open(AcceptModalComponent);
    return dialogRef.afterClosed();
  }

  addProduct(){
    const dialogRef = this.dialog.open(AddEditInventoryComponent);
    return dialogRef.afterClosed();
  }

  addCoupon(){
    const dialogRef = this.dialog.open(AddEditCouponComponent);
    return dialogRef.afterClosed();
  }
  
  addCharity(){
    const dialogRef = this.dialog.open(AddEditCharityComponent);
    return dialogRef.afterClosed();
  }

  addCompaign(){
    const dialogRef = this.dialog.open(AddEditCompaignComponent);
    return dialogRef.afterClosed();
  }

  addLoyalty(){
    const dialogRef = this.dialog.open(AddLoyaltyComponent);
    return dialogRef.afterClosed();
  }
}
